<template>
  <!-- <div class="wrapper"> -->
  <div class="index">
    <div class="news">
      <div class="containers-about">
        <div class="about-courses">
          <h1>Подробная информаци о Курсе “{{ courses.name }}”</h1>

          <div class="about-courses__row">
            <div class="about-courses__columns">
              <h2>Описание:</h2>
              <p>{{ courses.description }}</p>
            </div>
            <div class="about-courses__columns">
              <div class="about-courses__block">
                <h2>Срок обучения:</h2>
                <p>{{ courses.period }}</p>
              </div>
              <div class="about-courses__block">
                <h2>Цена:</h2>
                <p>{{ courses.price }} рублей</p>
              </div>
            </div>
          </div>
        </div>
        <!-- <p v-if="errors.length">
                        <b>Please correct the following error(s):</b>
                        <ul>
                            <li v-for="er in errors" :key="er.name">{{ errors }}</li>
                        </ul>
                    </p> -->
        <div class="contact">
          <h1>Хотите приобрести курс? Оставьте заявку, мы с вами свяжемся.</h1>
          <form action="" class="contact__form block" method="POST">
            <div class="block_info">
              <p>Ваши Имя и Фамилия</p>
              <input
                type="text"
                id="name"
                v-model="name"
                @blur="$v.name.$touch()"
                placeholder="Иваи Иванов"
              />
              <span v-if="$v.name.$error" class="span-error">
                <template v-if="!$v.name.maxLength">
                  Длина имени не должна превышать
                  {{ $v.name.$params.maxLength.max }} символов
                </template>
                <template v-else-if="!$v.name.alpha">
                  Введите Имя и Фамилию
                </template>
              </span>
            </div>
            <div class="block_info">
              <p>Ваш номер телефона</p>
              <input
                type="text"
                id="phone"
                v-model="phone"
                v-mask="'+7(###)###-####'"
                placeholder="+7(###)###-####"
                @blur="$v.phone.$touch()"
              />
              <span v-if="$v.phone.$error" class="span-error">
                <template v-if="!$v.phone.minLength">
                  Номер введен некорректно!
                </template>
              </span>
            </div>
            <div class="block_info">
              <p>Электронная почта</p>
              <input
                type="text"
                id="mail"
                v-model="mail"
                @blur="$v.mail.$touch()"
                placeholder="example@email.com"
              />
              <span v-if="$v.mail.$error" class="span-error">
                <template v-if="!$v.mail.validFormat">
                  Укажите корректный адрес электронной почты.
                </template>
              </span>
            </div>
            <span v-if="$v.$invalid" class="span-button">
              <template>
                <p>Перед отправкой данный заполните все поля!</p>
              </template>
            </span>
            <div class="block__btn btn">
              <a href="#popup" @mousedown="index">
                <button
                  type="submit"
                  value="Отправить"
                  class="btn__span"
                  :disabled="$v.$invalid"
                  @mousedown="
                    coursenamefun(courses);
                    postPost(name, phone, mail, productname);
                  "
                >
                  Отправить
                </button></a
              >
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
  <!-- </div> -->
</template>

<script>
import axios from "axios";
import TopMenu from "@/components/top-menu.vue";
import LeftMenu from "@/components/left-menu.vue";
import Aside from "@/components/aside.vue";
import Footer from "@/components/footer.vue";
import Employee from "@/components/employee.vue";
import {
  required,
  maxLength,
  minLength,
  email,
} from "vuelidate/lib/validators";
// import moment from 'moment';

export default {
  name: "more-course",
  data() {
    return {
      name: null,
      mail: null,
      phone: null,
      productname: [],
    };
  },
  form: {
    phone: "",
  },
  // Модель для валидации, которую Vuelidate превратит в computed-поле $v
  validations: {
    mail: {
      validFormat: (val) =>
        /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/.test(
          val
        ),
    },
    name: {
      required,
      maxLength: maxLength(30),
      alpha: (val) => /^[а-яё]+(?: |, |-)+[а-яё]*$/i.test(val),
    },
    phone: {
      minLength: minLength(15),
    },
  },
  computed: {
    courses() {
      return this.$store.getters.CourseById(this.$route.params.name);
    },
  },
  methods: {
    postPost(name, phone, mail, productname) {
      console.log("watch");
      try {
        let data = axios.post("http://31.31.203.119/api/orders", {
          name: name,
          mail: mail,
          phone: phone,
          productname: productname[0].name,
        });
        if (data.status == "success") {
          console.log("Data deliveried");
        } else {
          throw new Error(data.status);
        }
      } catch (e) {
        this.error = e;
      }
    },
    coursenamefun(courses) {
      this.productname.splice(courses, 1);
      this.productname.push({
        name: courses.name,
      });
    },
    index() {
      this.$router.push("/");
    },
  },

  components: {
    TopMenu,
    LeftMenu,
    Aside,
    Footer,
    Employee,
  },
};
</script>